<template>
    <div class="admininterface" ref="homePage">
        <el-container>
            <!-- header -->
            <el-header>
              <div class="fleft">
                后台管理
              </div>
              <div class="fright">
                <ul>
                  <li><i class="el-icon-setting"></i></li>
                  <!-- 待增加下拉列表 -->
                  <li>
                      <el-dropdown>
                          <span class="el-dropdown-link" >
                            管理员<i class="el-icon-arrow-down el-icon--right"></i>
                          </span>
                          <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item >个人信息</el-dropdown-item>
                            <el-dropdown-item @click.native="Logout()" >切换账号</el-dropdown-item>
                            <el-dropdown-item @click.native="Logout()" >退出</el-dropdown-item>
                          </el-dropdown-menu>
                      </el-dropdown>
                  </li>
                  <li @click="JumpTOIndex();">前台首页</li>
                </ul>
              </div>
            </el-header>

            
            <el-container>
                <!-- 侧边栏 -->
                <el-aside>
                    <el-menu
                      default-active="2"
                      class="el-menu-vertical"
                      :unique-opened="true"
                      :collapse="isCollapse"
                      background-color="#eee"
                      text-color="#333"
                      active-text-color="#0074e4"
                      router
                      >
                        <el-menu-item @click="change()" >
                            <i class="el-icon-more icon-black"></i>
                        </el-menu-item>
                        <!-- 加载侧边栏数据  start -->
                        <el-submenu v-for="(item,index) in menuList" :key="index" :index="item.id">
                            <template slot="title">
                                <i class="icon-black" :class="item.icon"></i>
                                <span>{{item.tit}}</span>
                            </template>
                            <el-menu-item v-for="child in item.children" :key="child.id" :index="child.index">
                                {{child.childtit}}
                            </el-menu-item>
                        </el-submenu>
                        <!-- 加载侧边栏数据  end -->
                    </el-menu>
                </el-aside>


                <!-- 中间主视图 -->
                <el-main>
                    <!-- tab页开始 -->
                    <el-tabs
                        v-model="activeIndex"
                        type="card"
                        closable
                        v-if="openTab.length"
                        @tab-click='tabClick'
                          @tab-remove='tabRemove'
                        >
                        <el-tab-pane
                          :key="item.name"
                          v-for="item in openTab"
                          :label="item.name"
                          :name="item.route"
                        >
                        <span slot="label" style="user-select: none;"><i :class="item.icon"></i>{{item.name}}</span>
                        </el-tab-pane>
                        <!-- 切换视图 -->
                        <div class="main-view">
                            <el-card>
                                <keep-alive>
                                    <router-view />
                                </keep-alive>
                            </el-card>
                        </div>
                    </el-tabs>
                    <!-- tab页结束 -->
                    <footer>2019 © 蓝桥第十小组</footer>
                </el-main>


            </el-container>
        </el-container>


    </div>
</template>



<script>
export default {
    data(){
        return {
            //////////////////////////////////
            clientHeight:'',    // 屏幕高度
            isCollapse: false,  // 侧边栏是否收起
            menuList : [        // 侧边栏数据源
                {
                    id : '1',
                    tit : '用户管理',
                    icon : 'el-icon-s-custom',
                    children : [
                        {
                            index : '/admininterface/usermanage/queryuser',
                            childtit : '查询用户'
                        },
                        {
                            index : '/admininterface/usermanage/adduser',
                            childtit : '添加用户'
                        }
                    ]
                },
                {
                    id : '2',
                    tit : '商品管理',
                    icon : 'el-icon-s-shop',
                    children : [
                        {
                            index : '/admininterface/productsmanage/queryproducts',
                            childtit : '查询商品'
                        },
                        {
                            index : '/admininterface/productsmanage/addproducts',
                            childtit : '添加商品'
                        }
                    ]
                },
                {
                    id : '3',
                    tit : '类别管理',
                    icon : 'el-icon-s-grid',
                    children : [
                        {
                            index : '/admininterface/categorymanage/querycategory',
                            childtit : '查询类别'
                        },
                        {
                            index : '/admininterface/categorymanage/addcategory',
                            childtit : '添加类别'
                        }
                    ]
                },
                {
                    id : '4',
                    tit : '订单管理',
                    icon : 'el-icon-s-order',
                    children : [
                        {
                            index : '/admininterface/ordermanage/queryorder',
                            childtit : '查询订单'
                        }
                    ]
                }
            ]


        }
    },
    created(){
        console.log("当前用户：" + window.sessionStorage.getItem("loginAdminId"));
        if(this.$store.state.loginAdminId){
            // 临时解决方案

            // 进入页面
        }else{
            alert("请先登录管理员账号！")
            this.$router.replace({path:'/adminlogin'});
        }
    },
    computed: {
        openTab () {
            return this.$store.state.openTab;
        },
        activeIndex:{
            get () {
                return this.$store.state.activeIndex;
            },
            set (val) {
                this.$store.commit('set_active_index', val);
            }
        }
    },
    destroyed() {
        // 在 destroyed 钩子卸载事件
        // 会触发刷新事件
        window.removeEventListener('beforeunload', function(){ console.log("e => this.beforeunloadFn(e)e => this.beforeunloadFn(e)e => this.beforeunloadFn(e)")});
    },
    mounted(){
        // 在 mounted 或者 activated 钩子中注册事件
        window.addEventListener('beforeunload', e => this.beforeunloadFn(e))


        // 初始化路由状态
        // 待改进，openTab非空显示其缓存
        if (this.$route.path == '/admininterface/mainpage') {
            this.$store.commit('add_tabs', {route: '/admininterface/mainpage' , name: '主页', icon:'el-icon-s-home'});
        }else{
            this.$store.commit('add_tabs', {route: '/admininterface/mainpage' , name: '主页', icon:'el-icon-s-home'});
            this.$store.commit('add_tabs', {route: this.$route.path , name: this.$route.name, icon:''});
        }
        this.$store.commit('set_active_index', this.$route.path);


        //////////////////////////////////
        // 获取浏览器可视区域高度
        this.clientHeight =   `${document.documentElement.clientHeight}`          
        window.onresize = function temp() {
            this.clientHeight = `${document.documentElement.clientHeight}`;
        };
    },
    watch:{
        '$route'(to,from){
            //判断路由是否已经打开
            //已经打开的 ，将其置为active
            //未打开的，将其放入队列里
            let flag = false;
            for(let item of this.openTab){
                if(item.name == to.name){
                flag = true;
                break;
                }
            }
            if(!flag){
                this.$store.commit('add_tabs', {route: to.path, name: to.name,icon : '123'});
            }
            

            // 设置当前active
            this.$store.commit('set_active_index',to.path);
        },

        //////////////////////////////////
        // 如果 `clientHeight` 发生改变，这个函数就会运行
        clientHeight: function () {
            this.changeFixed(this.clientHeight)
        }
    },
    methods: {
        //////////////////////////////////
        changeFixed(clientHeight){ //动态修改样式
            console.log(clientHeight);
            this.$refs.homePage.style.height = (clientHeight-1)+'px';
        },

        // 离开页面时调用的方法
        beforeunloadFn(){
            console.log("离开后台管理页面。");
            // 清除sessionStorage登录状态
            // 会触发刷新。待解决
            // this.$store.dispatch("logoutAdmin");
        },
        Logout(){
            this.$store.dispatch("logoutAdmin");
            console.log("退出登录。");
            this.$router.replace({path : '/adminlogin'});
        },


        // 改变侧边栏状态
        change(){
            this.isCollapse = !this.isCollapse;
        },
        // 点击tab切换路由
        tabClick(tab){
            // 在open中就加入路由
            this.$router.replace({path: this.activeIndex}); // 跳转更新显示
        },
        // 删除tab标签
        tabRemove(targetName){
            //首页不删
            if(targetName == '/admininterface/mainpage'){
                return;
            }
            // 提交删除
            this.$store.commit('delete_tabs', targetName);
            // 设置当前激活的路由
            if (this.activeIndex == targetName) {
                if (this.openTab && this.openTab.length >= 1) {
                    this.$store.commit('set_active_index', this.openTab[this.openTab.length-1].route);
                    this.$router.replace({path: this.activeIndex});  // 跳转更新显示
                } 
            }
        },
        // 跳转至前台首页
        JumpTOIndex(){
            // 设置不可回退/新窗口打开
            this.$router.replace({path:'/index'});
        }
        // methods结束
    }
}
</script>


<style >
    .admininterface{
       /* position: fixed;
       top: 0;
       right: 0;
       bottom: 0;
       left: 0; */
    }

    /* 布局 */
    .admininterface .el-header{
        background-color: #222;
        color:#eee;
        /* color:#99CC66; */
        line-height: 40px;
        height: 40px !important;
        font-size: 13px;
    }
    .admininterface .el-aside{
        background-color: #fff;
        width: auto !important;
      }
    .admininterface .el-main{
        background-color: #f7f7f6;
        position: relative;
        padding: 0;
        overflow: hidden;
    }

    /* 左侧导航栏菜单 */
    .admininterface .el-menu-vertical{
        height: 100%;
        /* height: 681px; */
    }
    .el-menu-vertical:not(.el-menu--collapse) {
        width: 200px;
    }
     .admininterface .icon-black{
        color: #555;
        font-size: 20px;
        margin-right: 10px;
    }

    /* 顶部 */
    .el-header ul{
        list-style: none;
    }
    .el-header ul li{
        color:#eee;
        float: left;
        padding-right: 15px;
    }
    .el-header ul li:hover{
        color:#409EFF;
        cursor: pointer;
    }
    /* 下拉列表 */
    ul li .el-dropdown-link {
        font-size: 13px;
        color:#eee;
        float: left;
    }
    .el-dropdown-link:hover {
        color:#409EFF;
    }

    /* 标签页 */
    .el-tabs{
        height: 680px;
        overflow: hidden;
    }
    /* 需要去除scoped限制，加上!important修饰 */
    .el-tabs__content{
        padding: 0px 20px 5px 10px !important;
    }

    .main-view{
        height: 600px;
        overflow:auto;
    }

    .el-card__body{
        min-height: 550px;
    }

    /* 底部 */
    footer{
        background-color:#E0E0E0;
        position:absolute;
        width: 100%;
        bottom: 0;
        box-sizing: border-box;
        padding: 0 20px;
        text-align: left;
        line-height: 24px;
        height: 24px;
        font-weight: 200;
        font-size: 12px;
    }

    /* 备注 
      子菜单颜色 #434A50
    */

</style>


